@import url(https://fonts.googleapis.com/css?family=Rubik);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: 'Rubik', sans-serif;
}

.app {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background: #212E3F;
  text-transform: uppercase;
  color: white;
  font-weight: bold;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html {
  height: 100%;
}

.app {
  height: 100%;
  overflow: hidden;  /*makes the body non-scrollable (we will add scrolling to the sidebar and main content containers)*/
  margin: 0;  /*removes default style*/
  display: flex;  /*enables flex content for its children*/
  box-sizing: border-box;
}

.column {
  height: 100%;  /*allows both columns to span the full height of the browser window*/
  display: flex;
  flex-direction: column;  /*places the left and right headers above the bottom content*/
}

#left {
  /*background: #604f6d;*/
  border-right: solid 1px #efefef;
  height: 100vh;
  /*box-shadow: 2px -1px 5px 0 rgba(184,184,184,1);*/
  flex-shrink: 0;  /*makes sure that content is not cut off in a smaller browser window*/
  width: 80px;
  /*background-color: #7f53ac;*/
  /*background-image: linear-gradient(315deg, #7f53ac 0%, #647dee 74%);*/
}

#right {
  /*background: #f9f9f9;*/
  height: 100vh;
  width: 100vw;
}

#right .up {
  width: 100%;
  height: 70px;
  background: white;
  margin-bottom: 50px;
  border-bottom: solid 1px #efefef;
}

.topLeft {
  flex-shrink: 0;
  height: 70px;
  border-bottom: solid 1px #efefef;
}

.topRight {
  flex-shrink: 0;
  display: inline-flex;
}

ul {
  display: inline-flex;
  list-style: none;
}

.bottom {
  flex-grow: 1;  /*ensures that the container will take up the full height of the parent container*/
  overflow-y: auto;  /*adds scroll to this container*/
}

.fa-clock {
  font-size: 40px;
  margin-top: 15px;
  color: #6f5873;
  background: -webkit-gradient(linear, left top, left bottom, from(#8e3667), to(#4d2a8a));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  /*#373a4c*/
}

.header {
  height: 60px;
  background: white;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #E1ECF5;
  text-transform: uppercase;
  font-weight: bold;
}

.header .title {
  color: #9CB0C0;
  margin-left: 5em;
}

.container {
  margin: 2em 5em;
  text-align: left;
}

p {
  font-weight: 400;
  font-size: 1.2em;
  margin-bottom: 1.5em;
  color: #777;
}

/* styles below are from Alan Smith's D3 chart used in the example
http://bl.ocks.org/alansmithy/e984477a741bc56db5a5 */
svg {
  width: 500px;
  height: 500px;
}
button {
  float: left;
}
line {
  stroke: #ddd;
  shape-rendering: crispEdges;
}
text {
  text-anchor: middle;
}
circle {
  fill: orange;
  stroke: orange;
  fill-opacity: 0.5;
}
.axis line {
  fill: none;
  stroke: #ddd;
  shape-rendering: crispEdges;
}
.axis path  {
  fill: none;
}
.axis text {
  font-size: 0.7em;
  fill: #555;
  font-family: sans-serif;
}

#calendar {
  margin: 20px;
}
.month {
  /*margin-right: 8px;*/
}
.month-name {
  /*font-size: 85%;*/
  fill: #777;
}
.day.hover {
  stroke: #6d6E70;
  stroke-width: 2;
}
.day.focus {
  stroke: #ffff33;
  stroke-width: 2;
}
.tooltip {
  display:block;
  position:fixed;
  overflow:hidden;
  background: white;
  font-weight: bold;
  padding: 14px 20px;
  border: solid 1px #e8e8e8;
  border-radius: 10px;
  opacity: .8;
  line-height: 30px;
}

#calendar svg {
  width: auto;
  height: auto;
}

#calendar g {
  position: absolute;
  line-height: 1.7em;
  font-size: 85%;
}

#calendar .day {
  cursor: pointer;
}

.dayLabels {
  display: block;
  color: #777;
}

.months {
  margin-left: 40px;
}

.tooltip-hidden {
  opacity: 0;
}

.tooltip-visible {
  opacity: 1;
  position: absolute;
  text-align: center;
  width: 60px;
  height: 28px;
  padding: 2px;
  font: 12px sans-serif;
  background: lightsteelblue;
  border: 0;
  border-radius: 8px;
  pointer-events: none;
}

.tooltip .text {
  font-weight: 400 !important;
}

